.message {
    color: black;
    margin: 0.5rem;
    max-width: calc(100% - 60px);
    text-align: left;
}

.message-time {
    color: darkgrey;
    font-size: x-small;
    margin-left: 1rem;
}

.message-inbound {
    border-radius: 0px 6px 6px 6px;
    background-color: #FAEBD7;
}

.message-outbound {
    background-color: #F0F8FF;
    border-radius: 6px 0px 6px 6px;
    margin-left: auto;
}